Customizing..
************/
.scroll::-webkit-scrollbar {
  width: 11px;  /* 스크롤바의 너비 */
}

.scroll::-webkit-scrollbar-thumb {
  height: 30%; /* 스크롤바의 길이 */
  background: rgb(129 140 248); /* 스크롤바의 색상 */
  
  border-radius: 10px;
}

.scroll::-webkit-scrollbar-track {
  border-radius: 10px;
  background: rgb(238 242 255);  /*스크롤바 뒷 배경 색상*/
}

.switch {
  position: relative;
  display: inline-block;
  width: 110px;
  height: 28px;
  vertical-align:middle;
  align-items: center;
  margin-left: 7px;
}

/* Hide default HTML checkbox */
.switch input {display:none;}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .5s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.5s
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  transform: translateX(82px);
}
/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

input:focus{
  outline-color: rgb(109 40 217);
}

input{
  border-radius: 6px;
}

